import React, {Component} from 'react'
import YouTube from 'react-youtube'
import {Row, Select, Col} from 'antd'

const { Option } = Select;



class Music extends Component {

    constructor(props){
        super(props);
        this.state = {
            counter: 0,
            meredithQuillPlayList: ["DGDyAb6pePo", "0LSVta2GJpM", "RoR7wEEvIuo", "TazHNpt6OTo"],
            genericPlayList: ["p9lNpWa4OXU", "X3H-4crGD6k", "0IA3ZvCkRkQ", "YTaWayUE5XA", "0yBnIUX0QAE"],
            currentSong: "p9lNpWa4OXU",
            currentPlaylist: "generic"
        }

        this.playlist = {
            "meredithQuillPlaylist": ["DGDyAb6pePo", "0LSVta2GJpM", "RoR7wEEvIuo", "TazHNpt6OTo"],
            "genericPlaylist": ["p9lNpWa4OXU", "X3H-4crGD6k", "0IA3ZvCkRkQ", "YTaWayUE5XA", "0yBnIUX0QAE"]
        }

    }

    componentDidMount(){
    }

    componentDidUpdate(){

    }

    changeSong = () => {
        this.setState({
            currentSong: this.state[this.state.currentPlaylist + "Playlist"][Math.floor(Math.random() * this.state[this.state.currentPlaylist + "Playlist"].length)]
        })
    }

    changePlaylist = (val) => {
        const str = val + "Playlist"
        this.setState({
            currentPlaylist: val,
            currentSong: this.playlist[str][Math.floor(Math.random() * this.playlist[str].length)]
        })
    }

	render(){

        const opts = {
            height: '390',
            width: '640',
            playerVars: {
                // https://developers.google.com/youtube/player_parameters
                autoplay: 1,
            }
        };

        return (
            <div>
                <Row><h2>Music</h2></Row>
                <Row><h4>Get a dose of what I am listening to! Hope you'll enjoy!</h4></Row>
                <br></br>
                <Row>
                    <div style={{marginRight:"10px", marginTop:"5px"}}><h4>Select Playlist: </h4></div>
                    <Select defaultValue={this.state.currentPlaylist} onChange={this.changePlaylist}>
                        <Option value="generic">Generic</Option>
                        <Option value="meredithQuill">Meredith Quill</Option>
                    </Select>
                </Row>
                <br></br>
                <Row>
                    <YouTube videoId={this.state.currentSong} opts={opts} onReady={this._onReady} onEnd={this.changeSong} />
                </Row>
            </div>
        );
	}
	
}

export default Music;